body {
  margin: 0;
  font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
  background-color: #121212;
  color: #fff;
}

.App {
  text-align: center;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
}

.hero {
  background-color: #1f1f1f;
  padding: 4rem 2rem;
  border-bottom: 2px solid #333;
}

.hero h1 {
  font-size: 3rem;
  margin-bottom: 1rem;
}

.hero p {
  font-size: 1.25rem;
  color: #bbb;
}

.content {
  padding: 2rem;
  flex: 1;
}

h2 {
  font-size: 2rem;
  margin-bottom: 1.5rem;
}

.anime-list {
  list-style: none;
  padding: 0;
  max-width: 600px;
  margin: 0 auto;
}

.anime-list li {
  background: #2a2a2a;
  margin-bottom: 1rem;
  padding: 1rem 1.5rem;
  border-radius: 8px;
  text-align: left;
  display: flex;
  align-items: center;
  justify-content: space-between;
  transition: background 0.3s;
}

.anime-list li:hover {
  background: #333;
}

.rank {
  color: #00d8ff;
  font-weight: bold;
}

footer {
  padding: 1rem;
  background-color: #1f1f1f;
  font-size: 0.9rem;
  color: #aaa;
}
